.join__us__form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    width: 100%;
    margin: 40px auto 0;
  }
  .join__us__form > .sub__section {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .join__us__form > .sub__section > .input {
    width: 400px;
    margin: 10px;
  }
  .join__us__form > .sub__section > .input fieldset {
    border-radius: 50px;
  }
  .MuiFormLabel-root.Mui-focused {
    color: #56b124 !important;
  }
  .MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
    border-color: #56b124 !important;
  }
  .join__us__form > .sub__section > label {
    padding: 17px 0;
    font-size: 1.1rem;
    cursor: pointer;
    border-radius: 50px;
    width: 400px;
    text-align: center;
    color: #fff;
    margin: 10px;
  }
  .join__us__form > button {
    padding: 12px 40px;
    background-color: #56b124;
    color: #fff;
    cursor: pointer;
    border: none !important;
    outline: none !important;
    border-radius: 50px;
    font-size: 1.1rem;
    transition: 0.3s;
    margin-top: 20px;
  }
  .join__us__form > button:hover {
    background-color: #05242a;
  }
  
  /* 1024px */
  @media only screen and (max-width: 1024px) {
    .join__us__form > .sub__section {
      flex-direction: column;
    }
    .join__us__form > .sub__section > .input {
      margin: 10px 0;
    }
    .join__us__form > button {
      margin-top: 10px;
    }
  }
  
  /* 480px */
  @media only screen and (max-width: 480px) {
    .join__us__form > .sub__section {
      width: 330px;
    }
  
    .join__us__form > .sub__section > .input,
    .join__us__form > .sub__section > label {
      width: 100%;
    }
  }
  