.main__section > h1 {
    text-align: center;
    font-size: 2.5rem;
    margin-top: 50px;
  }
  
  /* 1024px */
  @media only screen and (max-width: 1024px) {
    .main__section > h1 {
      font-size: 2.2rem;
    }
  }
  
  /* 480px */
  @media only screen and (max-width: 480px) {
    .main__section > h1 {
      font-size: 1.6rem;
    }
  }
  